<template>
  <!--  目录后续优化 -->
  <view class="main">
    <!-- 书籍内容展示开始 -->
        <swiper class="swiper"  @change="swiperMove" :current="currentIndex" @click="showBox">
              <swiper-item class="swiper-item" v-for="(content,index) in chapterPages" :key="index">
                <text class="swiper-item-title">
                {{Array.isArray(content)?content[0]:''}}
                </text>
                <text>
                {{Array.isArray(content)?content[1]:content}}
                </text>
            </swiper-item>
          </swiper>
    <!-- 书籍内容展示结束 -->
    
    <!-- 上方悬浮框开始 -->
    <view class="main-top" v-if="isShow">
      <text v-if="isShowBookrack" @click="buttonAdd">加入书架</text>
      <text v-else @click="buttonDelete">移出书架</text>
    </view>
    <!-- 上方悬浮框结束 -->
    
    <!-- 下方悬浮框开始 -->
    <view class="main-bottom" v-if="isShow">
      <view class="icon">
        <uni-icons type="list" size="38" @click="showDrawer"></uni-icons>
      </view>
      <view class="icon">
        <uni-icons type="color" size="38"></uni-icons>
      </view>
      <view class="icon">
        <uni-icons type="font" size="38"></uni-icons>
      </view>
    </view>
    <!-- 下方悬浮框结束 -->
    
    <!-- 目录开始 -->
    <uni-drawer ref="showRight" mode="left" class="uni-drawer" id="showRight" :width="250">
    		<scroll-view style="height: 100%;" scroll-y="true" class="uni-drawer-scroll">
    				<view v-for="(list,index) in chapterList" :key="index" class="list">
              <text @click="intoReadBook(index)">{{list.title}}</text>
            </view>
    		</scroll-view>
    </uni-drawer>
    <!-- 目录结束 -->
  </view>
</template>

<script>
  import {mapState,mapMutations } from 'vuex'
  export default {
    data() {
      return {
        book:{},
        //书籍id
        id:'',
        //章节列表
        chapterList:[],
        //每页显示多少字
        pageSize:500,
        //当前页数
        page:1,
        //每页的数据
        chapterPages:[],
        // 是否正在请求数据
        isloading: false,
        //章节索引
        chapterIndex:0,
        //控制不重复发送请求
        isloading:false,
        //控制 加入书架是否显示
        isShowBookrack:false,
        //控制上下方弹框是否显示
        isShow:false,
        //swiper的current
        currentIndex:0,
        //书架的数据
        books:[]
      };
    },
    onLoad(options) {
      this.book = JSON.parse(options.book||'[]')
      //获取书架的数据
      this.books = JSON.parse(uni.getStorageSync('bookrack')||'[]')
       //获取id 以请求章节
       this.id = this.book.fictionId
        //查看本地是否有章节索引
        this.chapterIndex = uni.getStorageSync(this.id+'-chapterIndex') || 0
        //查看本地是否有小说目录
        this.chapterList = JSON.parse(uni.getStorageSync(this.id+'-chapterList')||'[]')
        //查看本地是否有小说内容
        this.chapterPages = JSON.parse(uni.getStorageSync(this.id+'-chapterPages') || '[]')
        //如果小说内容为空 就请求第一章小说的内容
        if(!this.chapterPages.length){
          //请求目录
          (async ()=>{
            await this.getBookChapter()
            this.getBookContent(this.chapterList[0].chapterId)
          })()
        }
        //判断是否在书架
        this.isInBookrack()
    },
    onReady() {
      //取出current
      const currentIndex = uni.getStorageSync(this.id+"currentIndex");
      if(currentIndex){
        this.currentIndex = currentIndex
      }
    },
    onUnload(){
      //将current存入本地
      let currentIndex = Number(this.currentIndex)
      uni.setStorageSync(this.id+"currentIndex",currentIndex);
      
      //将章节索引存入本地
      uni.setStorageSync(this.id+'-chapterIndex',this.chapterIndex)
      //判断书籍是否在书架中 如果不在书架，就清除本地保存的数据
      //加入书架展示 意味着在书架 否则不在书架 清除数据
      if(this.isShowBookrack){
        uni.removeStorageSync(this.id+'-chapterList')
        uni.removeStorageSync(this.id+'-chapterPages')
        uni.removeStorageSync(this.id+'currentIndex')
        uni.removeStorageSync(this.id+'-chapterIndex')
      }
    },
    methods:{
      //添加书籍 本地存储 更新书籍
      ...mapMutations('book', ['addBooks','saveStorage','updateBooks']),
      //获取小说章节
      async getBookChapter(){
        let result = await uni.$http.get(`/fictionChapter/search/${this.id}`)
        if(result.statusCode == 200){
          this.chapterList = result.data.data.chapterList
          //将章节存入本地
          uni.setStorageSync(this.id+'-chapterList',JSON.stringify(this.chapterList))
        }else{
          uni.$showMsg()
        }
        let chapterId = this.chapterList[this.chapterIndex].chapterId
        if(chapterId && !this.chapterPages.length){
          //内容
          this.getBookContent(chapterId)
        }
      },
      //获取小说内容
      async getBookContent(id,isFirst){
        this.isloading = true
        let index =this.chapterIndex
        let result = await uni.$http.get(`/fictionContent/search/${id}`)
        if(result.statusCode == 200){
          let chapterContent = result.data.data
          //数据处理
          this.addChapter(index,id,chapterContent,isFirst)
          //将每章数据存入本地
          uni.setStorageSync(this.id+'-chapterPages',JSON.stringify(this.chapterPages))
        }else{
          uni.$showMsg()
        }
        this.isloading = false
      },
      //将内容添加进数组中
      addChapter(index,chapterId,chapterContent,isFirst){
        //每章内容数组 存放每页数据
        let pages= []
        //标题
        let chapterTitle = this.chapterList[index].title
        //总内容
        let chapterTotalContent = chapterTitle+'___'+chapterContent
        // let chapterLength = chapterTotalContent.length
        //进行分页
        pages = this.paging(pages,chapterTotalContent)
        let arr = pages[0].split("___")
        let titleArr = [arr[0],arr[1]];
        pages[0] = titleArr
        // let chapterTotalPage = Math.ceil(chapterLength/this.pageSize)
        // this.chapterTotalPage.push(chapterTotalPage)
        //请求的是未加载的上一章的数据
        // if(isFirst){
        //   //将pages由二维化为一维
        //   let pages2 = [...pages]
        //   this.chapterPages.unshift(pages2)
        //   return
        // }else{
        //   //合并数组
        //   this.chapterPages = [...this.chapterPages,...pages]
        // }
        //合并数组
          this.chapterPages = [...this.chapterPages,...pages]
        //去重
        this.chapterPages = [...new Set(this.chapterPages)]
      },
      //分页
      paging(pages,chapterTotalContent){
        while (chapterTotalContent.length > this.pageSize) {
            pages.push(chapterTotalContent.slice(0, this.pageSize));
            chapterTotalContent = chapterTotalContent.slice(this.pageSize);
        }
        pages.push(chapterTotalContent);
        return pages
      },
      //判断书籍是否在书架中
      isInBookrack(){
        this.isShowBookrack = true
        this.books.forEach((item)=>{
          if(item.fictionId == this.book.fictionId){
            this.isShowBookrack = false
          }
        })
      },
      //控制上下方弹框是显示 隐藏
      showBox(){
        this.isShow = !this.isShow
      },
      //目录显示
      showDrawer(){
        this.$refs.showRight.open();
      },
      //目录隐藏
      closeDrawer() {
      	this.$refs.showRight.close();
      },
      //滑动小说
      swiperMove(e){
        this.currentIndex = e.detail.current
        //如果是本章最后一页 就发送下一章的请求
        if(e.detail.current == this.chapterPages.length-1){
          // 判断是否正在请求其它数据，如果是，则不发起额外的请求
          if (this.isloading) return
          this.chapterIndex = Number(this.chapterIndex) + 1;
          //如果是最后一章 不再发送请求
          if(this.chapterIndex > this.chapterList.length)return
          let id = this.chapterList[this.chapterIndex].chapterId
          //请求章节内容
          this.getBookContent(id)
        }
      },
      //点击目录加载内容
      intoReadBook(index){
        //隐藏目录
        this.closeDrawer()
        //更新章节索引
        this.chapterIndex = index
        //将小说内容清空
        this.chapterPages = []
        //清除本地current
        uni.removeStorageSync(this.id+"currentIndex")
        //加载特定章节的内容
        this.getBookContent(this.chapterList[index].chapterId)
      },
      //获取前一章未加载的数据
      getPrevious(e){
        //滑动到第一页并且往左滑动
        if(this.currentIndex == 0 && e.detail.dx < 0){
          let index = this.chapterIndex 
          let isFirst = true
          //判断是否是第一章 如果是第一章就没事上一章的数据了
          if(index != 0){
            this.getBookContent(this.chapterList[index - 1].chapterId,isFirst)
          }
        }
      },
      //加入书架
      buttonAdd(){
        this.addBooks(this.book);
        this.saveStorage();
        this.isShow = false;
        this.isShowBookrack = false
      },
      // 删除书籍
      buttonDelete(){
          this.isShow = false
          let deleteBooks = this.books.filter((item)=>{
            return item.fictionId != this.book.fictionId
          })
          this.updateBooks(deleteBooks) 
          this.saveStorage();
          this.isShowBookrack = true
      },
    },
  }
</script>

<style lang="less">
.main{
  background-color: aquamarine;
  opacity: 0.5;
  position: relative;
  width: 100%;
  height: auto;
  position: relative;
  .main-top{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    text-align: center;
    background-color: aliceblue;
    height: 100rpx;
    text{
     height: 100rpx;
     line-height: 100rpx;
    }
  }
    .swiper{ 
      height: 100vh;
      width: 100vw;
      font-size: 38rpx;
      line-height: 1.5;
      .swiper-item{
           display: flex;
           flex-direction: column;
           overflow: hidden;
           .swiper-item-title{
             font-weight: bold;
             color: black;
           }
      }
    }
  .main-bottom{
    position: fixed;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: space-around;
    background-color: aliceblue;
    .icon{
      text-align: center;
      width: 250rpx;
    }
  }
  .uni-drawer{
    .uni-drawer-scroll{
      .list{
        display: flex;
        flex-direction: column;
        text{
          border-bottom: 2rpx solid #efefef;
          margin-bottom:20rpx;
          height: 40rpx;
          line-height: 40rpx;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
    }
  }
}
</style>
